/*@import 'sprite';*/
html, body, .page-container { height: 100%; }

.off-canvas-wrapper { backface-visibility: initial; }

.page-container { display: table; width: 100%; background: #fff; table-layout: fixed; }

.page-header, .page-content, .page-footer { display: table-row; height: 1px; }

.page-content { height: 100%; }

.heading { padding: 0 0 80px 0; }

.heading h3 { font-size: 48px; margin: 0; color: #0163aa; line-height: normal; }

.heading span { text-align: center; display: block; position: relative; }

.heading span:after { content: "\f10b"; font-size: 30px; background-color: white; display: table; margin: 0 auto; z-index: 2; color: #0163aa; position: relative; padding: 0 5px 0  5px; line-height: normal; }

.heading span:before { content: ''; display: block; position: absolute; left: 0; top: 16px; right: 0; height: 2px; max-width: 150px; margin: 0 auto; width: 100%; background: #0163aa; }

/* General Styles for Content
==========================================*/
#content { position: relative; z-index: 2; background-color: #FFF; }

.site-header { position: relative; height: calc(100vh - 45px); margin: 45px; margin-bottom: 0; background-color: #0163aa; overflow: hidden; box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.5), 0 0 0 15px #015796, 0 0 10px 16px rgba(0, 0, 0, 0.5), 0 0 0 30px #014372, 0 0 10px 31px rgba(0, 0, 0, 0.5), 0 0 0 45px #003154; }

.site-header:after { content: ""; position: absolute; left: 0; right: 0; bottom: 0; height: 55%; background-image: url(../img/header-bg.png); background-repeat: no-repeat; background-size: 200%; background-position: -390px 10px; z-index: 0; opacity: 0.85; display: block; }

.site-header.sticky-header .site-branding #top-header { position: fixed; top: 0; left: 45px; right: 45px; z-index: 50; padding: 10px 0; background-color: rgba(1, 99, 170, 0.9); box-shadow: 0 -2px 10px 1px rgba(0, 0, 0, 0.5), 0 -15px 0 15px #015796, 0 -17px 10px 16px rgba(0, 0, 0, 0.5), 0 -30px 0 30px #014372, 0 -32px 10px 31px rgba(0, 0, 0, 0.5), 0 -45px 0 45px #003154; }

.site-header.sticky-header .site-branding #top-header .site_logo { padding-top: 0; }

.site-header.sticky-header .site-branding #top-header .site_logo a img { height: 50px; }

.site-header.sticky-header .site-branding #top-header #site-navigation { padding-top: 6px; }

.site-header.sticky-header.full .site-branding #top-header { left: 0; right: 0; }

.site-header .site-branding { z-index: 1; }

.site-header .site-branding #top-header { position: relative; overflow: hidden; transition: all 0.3s ease-in-out; }

.site-header .site-branding #top-header .site_logo { float: left; padding-top: 20px; margin-left: 40px; transition: all 0.3s ease-in-out; }

.site-header .site-branding #top-header .site_logo a img { width: auto; height: 80px; transition: all 0.3s ease-in-out; }

.site-header .site-branding #top-header #site-navigation { float: right; padding-top: 53px; margin-right: 31px; transition: all 0.3s ease-in-out; }

.site-header .site-branding #top-header #site-navigation ul li a { font-size: 22px; color: #FFF; padding: 0.7rem 34px; text-shadow: 4px 3px 7px black; }

.site-header .site-branding .site-title, .site-header .site-branding .site-description { position: absolute; top: 170px; left: 0; right: 0; font-size: 10vh; text-align: center; line-height: 1; margin-bottom: 0; letter-spacing: 1.5px; text-shadow: 4px 3px 7px black; transition: all 0.3s ease-in-out; }

.site-header .site-branding .site-title a, .site-header .site-branding .site-description a { color: #FFF; }

.site-header .site-branding .site-description { font-size: 4vh; top: 300px; color: #FFF; }

.site-header #header-phone-slider { position: absolute; width: 100%; height: 70vh; margin: 0 auto; left: 0; right: 0; bottom: -80px; z-index: 2; }

.site-header #header-phone-slider .header-images-slider { position: absolute; height: 100%; z-index: 1; margin: 0 auto; left: 0; right: 0; margin-top: 99px; }

.site-header #header-phone-slider .header-images-slider .slick-list { width: 1150px; margin: 0 auto; }

.site-header #header-phone-slider .header-images-slider .slick-arrow { display: none !important; }

.site-header #header-phone-slider .header-images-slider .ph_slide { position: relative; height: 550px; background-repeat: no-repeat; background-size: 100%; border-radius: 40px 40px 0 0; opacity: 1; left: 0; right: 0; -webkit-transition: all 0.3s ease-in-out; -moz-transition: all 0.3s ease-in-out; -ms-transition: all 0.3s ease-in-out; -o-transition: all 0.3s ease-in-out; transition: all 0.3s ease-in-out; }

.site-header #header-phone-slider .header-images-slider .ph_slide:not(.slick-current) { opacity: 0.3; }

.site-header #header-phone-slider .header-images-slider .ph_slide:not(.slick-current).left-current { left: auto; right: -100px; }

.site-header #header-phone-slider .header-images-slider .ph_slide:not(.slick-current).right-current { left: -100px; right: auto; z-index: -1; }

.site-header #header-phone-slider .phone-frame { position: relative; width: 100%; max-width: 600px; margin: 0 auto; display: block; z-index: 2; }

.site-footer .site-copyright { background-color: #0163aa; background-image: url("../img/footer-background.png"); background-repeat: no-repeat; background-size: cover; background-position: center; min-height: 120px; }

.site-footer .site-copyright.fullWidth { width: 100%; margin-left: auto; margin-right: auto; max-width: initial; }

.site-footer .site-copyright a { color: white; font-family: 'Montserraat Regular', sans-serif; font-size: 24px; display: block; margin: 0 auto; }

.site-footer .site-copyright a p { margin-bottom: 0; }

.slick-slider { position: relative; display: block; box-sizing: border-box; -webkit-touch-callout: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }

.slick-list:focus { outline: none; }

.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-track, .slick-slider .slick-list { transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; margin-left: auto; margin-right: auto; }

.slick-track:before, .slick-track:after { content: ""; display: table; }

.slick-track:after { clear: both; }

.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; outline: none; display: none; }

[dir="rtl"] .slick-slide { float: right; }

.slick-slide img { display: block; }

.slick-slide.slick-loading img { display: none; }

.slick-slide.dragging img { pointer-events: none; }

.slick-initialized .slick-slide { display: block; }

.slick-loading .slick-slide { visibility: hidden; }

.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-arrow.slick-hidden { display: none; }

.slick-dots { margin: 0; list-style: none; }

.slick-dots button { text-indent: -999em; }
